import classnames from 'classnames';
import React, { forwardRef } from 'react';

import Icon from '#components/Icon';

import {
  BUTTON_AS_BUTTON,
  BUTTON_SIZE_SMALL,
  BUTTON_VARIANT_PRIMARY,
} from './Button.constants';
import { StyledButton } from './Button.styles';
import { Props } from './Button.types';

import LaunchSVG from '#icons/launch-outline-16.svg';

const launchIcon = <Icon size="small" svg={LaunchSVG} />;

const Button = forwardRef<HTMLButtonElement, Props>(function Button(
  {
    as = BUTTON_AS_BUTTON,
    children,
    forwardedAs,
    className,
    size = BUTTON_SIZE_SMALL,
    disabled = false,
    variant = BUTTON_VARIANT_PRIMARY,
    icon,
    name,
    value,
    type = BUTTON_AS_BUTTON,
    onClick,
    href,
    target,
    rel,
    id,
    isExternal,
    ...rest
  },
  ref,
) {
  const typeProps = {
    a: {
      href,
      target,
      rel,
      icon,
      onClick,
    },
    button: {
      name,
      value,
      disabled,
      type,
      onClick,
    },
  }[as];

  return (
    <StyledButton
      as={as}
      forwardedAs={forwardedAs}
      $variant={variant}
      $size={size}
      id={id}
      className={classnames(className, {
        'is-disabled': disabled && (as === 'a' || forwardedAs === 'a'),
      })}
      ref={ref}
      {...typeProps}
      {...rest}
    >
      {children}
      {icon || (isExternal && launchIcon)}
    </StyledButton>
  );
});

export default Button;
